.services {
  grid-row: 3 / 5;
  grid-column: 1 / 3;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  flex-wrap: wrap;
  background-image: url("../img/services-promo.jpg");
  background-size: 97rem 106rem;
  background-position: top right;
  background-repeat: no-repeat;
  background-color: $second_color;
  overflow: hidden;

  @media (min-width: $desktop_max) {
    background-size: 970px 1060px;
  }
}

.services__field {
  margin: 26rem 0 0 4.9rem;

  @media (min-width: $desktop_max) {
    margin: 260px 0 0 49px;
  }
}

.services__heading {
  margin: 0;
  font-size: 3rem;
  line-height: 3rem;
  font-weight: 400;
  text-align: left;
  color: $fourth_color;

  @media (min-width: $desktop_max) {
    font-size: 30px;
    line-height: 30px;
  }
}

.services__promo-text {
  margin: 2.9rem 0 0 0;
  font-size: 12rem;
  line-height: 10rem;
  font-weight: 700;
  text-align: left;
  color: $fourth_color;
  text-transform: uppercase;

  @media (min-width: $desktop_max) {
    margin: 29px 0 0 0;
    font-size: 120px;
    line-height: 100px;
  }
}

.services__text {
  width: 81rem;
  margin: 3.1rem 0 0 0;
  font-size: 3rem;
  line-height: 5rem;
  font-weight: 400;
  text-align: left;
  color: $fourth_color;

  @media (min-width: $desktop_max) {
    width: 810px;
    margin: 31px 0 0 0;
    font-size: 30px;
    line-height: 50px;
  }
}

.services__btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 27.2rem;
  height: 8.5rem;
  margin: 6.9rem 0 0 0;
  padding: 3.0rem 6rem;
  font-size: 2.5rem;
  line-height: 2.5rem;
  font-weight: 500;
  text-align: left;
  letter-spacing: 0;
  color: $fourth_color;
  background-color: $five_color;
  border-radius: 10px;

  @media (min-width: $desktop_max) {
    width: 272px;
    height: 85px;
    margin: 69px 0 0 0;
    padding: 30px 60px;
    font-size: 25px;
    line-height: 25px;
  }
}
