.promo {
  grid-row: 1 / 3;
  grid-column: 1 / 3;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  overflow: hidden;
  background-color: $second_color;
}

.promo__slogan {
  width: 100%;
  margin-top: 19.9rem;

  @media (min-width: $desktop_max) {
    margin-top: 199px;
  }
}

.promo__slogan-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
}

.promo__slogan p {
  font-size:  28rem;
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
  margin: 0;

  @media (min-width: $desktop_max) {
    font-size:  280px;
  }
}

.promo__slogan-dark {
  color: $third_color;
  margin-right: 20rem;

  @media (min-width: $desktop_max) {
    margin-right: 200px;
  }
}

.promo__slogan-white {
  color: $fourth_color;
}

.promo__field {
  align-self: flex-end;
  width: 53.6rem;
  margin: 5.0rem 28.1rem 0 0;
  word-spacing: 0.1px;

  @media (min-width: $desktop_max) {
    width: 536px;
    margin: 50px 281px 0 0;
    word-spacing: 1px;
  }
}

.promo__text p {
  margin: 0;
  font-size: 3.9rem;
  line-height: 5rem;
  font-weight: 300;
  text-align: left;
  color: $fourth_color;

  @media (min-width: $desktop_max) {
    font-size: 39px;
    line-height: 50px;
  }
}

.promo__btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 32.7rem;
  height: 8.5rem;
  margin: 5.1rem 0 0 0;
  font-size: 2.5rem;
  line-height: 2.5rem;
  font-weight: 500;
  text-align: left;
  letter-spacing: .1.7rem;
  color: $fourth_color;
  background-color: $five_color;
  border-radius: 10px;
  border: none;

  @media (min-width: $desktop_max) {
    width: 327px;
    height: 85px;
    margin: 51px 0 0 0;
    font-size: 25px;
    line-height: 25px;
    letter-spacing: 1.7px;
  }
}
