@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Montserrat Regular"),
  local("Montserrat-Regular"),
  url('../fonts/montserrat-400.woff2') format('woff2'),
  url('../fonts/montserrat-400.woff') format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Montserrat Medium"),
  local("Montserrat-Medium"),
  url('../fonts/montserrat-500.woff2') format('woff2'),
  url('../fonts/montserrat-500.woff') format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Montserrat SemiBold"),
  local("Montserrat-SemiBold"),
  url('../fonts/montserrat-600.woff2') format('woff2'),
  url('../fonts/montserrat-600.woff') format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  src: local("Montserrat Bold"),
  local("Montserrat-Bold"),
  url('../fonts/montserrat-700.woff2') format('woff2'),
  url('../fonts/montserrat-700.woff') format('woff');
}
