.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  min-width: 320px;
  background-color: $second_color;
}

.header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: start;
  flex-wrap: wrap;
  width: 100%;
  min-width: 320px;
  max-width: 1920px;
  padding: 0 50px 0 50px;

  @media (max-width: 1440px) {
    padding: 10px;
  }

  @media (max-width: 500px) {
    flex-direction: column;
  }
}

.header__logo {
  min-width: 300px;
  width: 300px;
  height: 76px;
  margin: 30px 15px 0 0;
  order: 0;

  @media (max-width: 1600px) {
    flex-grow: 1;
  }

  & img {
    width: 300px;
    height: 76px;
  }
}

.header__top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  min-width: 200px;
  order: 0;

  @media (max-width: 1600px) {
    width: 30%;
  }
}

.header__link-lang {
  margin: 56px 0 0 15px;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-align: center;
  color: $main_color;

  @media (max-width: 1600px) {
    order: 2;
    margin: 56px 20px 0 15px;
  }
}

