.main {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  width: 100%;
  min-width: 320px;
  margin: -3px 0 0 0;
  background-color: $second_color;
}

.main__promo-services-wrapper {
  display: grid;
  grid-template-columns: 98.2rem 93.6rem;
  grid-template-rows: minmax(73.3rem, max-content) minmax(22.1rem, max-content) minmax(21.5rem, max-content) minmax(84.4rem, max-content);

  @media (min-width: $desktop_max) {
    grid-template-columns: 982px 936px;
    grid-template-rows: minmax(733px, max-content) minmax(221px, max-content) minmax(215px, max-content) minmax(844px, max-content);
  }
}

.main-tentacle {
  grid-row: 2 / 4;
  grid-column: 1 / 2;
  z-index: 2;
}

.main-tentacle-wrapper {
  width: 100%;
  height: 100%;
  background-image: url("../img/tentacle.png");
  background-size: 97.9rem 44.5rem;
  background-repeat: no-repeat;

  @media (min-width: $desktop_max) {
    background-size: 979px 445px;
  }
}
