body {
  width: 100%;
  font-family: $font;
  font-size: 16px;
  font-weight: 400;
  margin: 0;
}

html {
  font-size: calc(#{$base_font} * 100vw / 1920);
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually_hidden:not(:active),
input[type="checkbox"].visually_hidden,
input[type="radio"].visually_hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.circle {
  position: absolute;
  width: 98px;
  height: 98px;
  border-radius: 50%;
  background-color: $six_color;
}

a:hover,
button:hover {
  box-shadow: 0 0 30px 5px $main_color;
  background-color: $eight_color;
  color: $five_color;

  & a {
    color: $five_color;
  }
}
