.main-nav {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  margin: 56px 138px 0 0;

  @media (max-width: 1600px) {
    order: 3;
    flex-grow: 1;
    width: 90vw;
    margin: 56px 0 0 0;
  }

  @media (max-width: 500px) {
    align-self: center;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;

  @media (max-width: 1600px) {
    flex-grow: 1;
  }

  @media (max-width: 500px) {
    flex-direction: column;
  }
}

.main-nav__item {
  position: relative;
  list-style: none;
  margin: 0 0 15px 40px;

  @media (max-width: 500px) {
    margin: 0 10px 15px 10px;
  }
}

.main-nav__link {
  min-width: 130px;
  font-size: 20px;
  line-height: 20px;
  font-weight: 600;
  text-align: center;
  color: $main_color;
}
